.container {
    max-width: 600px; /* changed from width to max-width */
    margin: auto;
    height: 650px;
    margin-top: 75px;
    border-radius: 40px;
    background-image: linear-gradient(180deg, #eb6e4b, #3b2f80 100%);
    flex-direction: column;
    align-items: center;
    padding: 20px; /* added padding for smaller screens */
  }
  
  .top-bar {
    display: flex;
    justify-content: center;
    gap: 14px;
    padding-top: 60px;
  }
  
  .top-bar input {
    width: 100%; /* changed from fixed width to 100% */
    height: 78px;
    background: #ebfffc;
    border: none;
    outline: none;
    border-radius: 40px;
    padding-left: 40px;
    color: #626262;
    font-size: 20px;
    font-weight: 400;
    margin-bottom: 20px; /* added margin bottom for smaller screens */
  }
  
  .search-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 78px;
    height: 78px;
    background: #ebfffc;
    border-radius: 40px;
    cursor: pointer;
  }
  
  .weather-image {
    margin-top: 39px;
    display: flex;
    height: 140px;
    justify-content: center;
  }
  
  .weather-temp {
    display: flex;
    justify-content: center;
    color: white;
    font-size: 100px;
    font-weight: 400;
  }
  
  .weather-location {
    display: flex;
    justify-content: center;
    color: white;
    font-size: 40px;
    font-weight: 400;
  }
  
  .data-container {
    margin-top: 50px;
    color: white;
    display: flex;
    justify-content: center;
    flex-wrap: wrap; /* added flex-wrap for smaller screens */
  }
  
  .element {
    margin: auto;
    display: flex;
    align-items: flex-start;
    gap: 12px;
    margin-bottom: 20px; /* added margin bottom for smaller screens */
  }
  
  .data {
    font-size: 34px;
    font-weight: 400;
  }
  
  .text {
    font-size: 20px;
    font-weight: 400;
  }
  
  .icon {
    margin-top: 10px;
  }
  
  /* added media queries for smaller screens */
  @media (max-width: 768px) {
   .container {
      padding: 10px;
    }
   .top-bar input {
      width: 80%;
    }
   .weather-image {
      height: 100px;
    }
   .weather-temp {
      font-size: 60px;
    }
   .weather-location {
      font-size: 30px;
    }
   .data-container {
      flex-direction: column;
    }
   .element {
      margin-bottom: 10px;
    }
  }
  
  @media (max-width: 480px) {
   .container {
      padding: 5px;
    }
   .top-bar input {
      width: 70%;
    }
   .weather-image {
      height: 80px;
    }
   .weather-temp {
      font-size: 40px;
    }
   .weather-location {
      font-size: 20px;
    }
   .data-container {
      flex-direction: column;
    }
   .element {
      margin-bottom: 5px;
    }
  }